.poster_cards_3_pagePart{font-size:1rem;display:flex;flex-direction:column;justify-content:center;gap:50px}.poster_cards_3_pagePart .cards{display:grid;grid-template-columns:repeat(2, minmax(300px, 1fr));gap:20px}.poster_cards_3_pagePart .card{display:flex;flex-direction:column;gap:10px;padding:20px;border-radius:8px;overflow:hidden;min-height:200px}.poster_cards_3_pagePart .card .heading{font-size:1.2em;color:var(--first-text-color, white);border-left:4px solid var(--primary-color, teal);padding-left:10px}.poster_cards_3_pagePart .card .description{font-size:1em;color:var(--second-text-color, #f5f5f5)}